
.md-input-container{width:100%;min-height:48px;margin:4px 0 24px;padding-top:16px;position:relative
}
.md-input-container:after{height:1px;position:absolute;right:0;bottom:0;left:0;background-color:rgba(0,0,0,0.12);transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1);content:" "
}
.md-input-container label{position:absolute;top:23px;left:0;pointer-events:none;transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1);transition-duration:.3s;color:rgba(0,0,0,0.54);font-size:16px;line-height:20px
}
.md-input-container input,.md-input-container textarea{width:100%;height:32px;padding:0;display:block;border:none;background:none;transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1);transition-property:font-size;color:rgba(0,0,0,0.54);font-family:inherit;font-size:1px;line-height:32px
}
.md-input-container input:focus,.md-input-container textarea:focus{outline:none
}
.md-input-container input::-webkit-input-placeholder,.md-input-container textarea::-webkit-input-placeholder{color:rgba(0,0,0,0.54);font-size:16px;text-shadow:none;-webkit-text-fill-color:initial
}
.md-input-container textarea{min-height:32px;max-height:230px;padding:5px 0;resize:none;line-height:1.3em
}
.md-input-container .md-error{height:20px;display:block !important;position:absolute;opacity:0;transform:translate3d(0, -8px, 0);transition:all .3s cubic-bezier(0.55, 0, 0.55, 0.2);font-size:12px
}
.md-input-container .md-count{height:20px;position:absolute;right:0;font-size:12px
}
.md-input-container.md-input-placeholder label{pointer-events:auto;top:10px;opacity:0;font-size:12px
}
.md-input-container.md-input-placeholder input,.md-input-container.md-input-placeholder textarea{font-size:16px
}
.md-input-container.md-input-focused label,.md-input-container.md-has-value label{pointer-events:auto;top:0;opacity:1;font-size:12px
}
.md-input-container.md-input-focused input,.md-input-container.md-input-focused textarea,.md-input-container.md-has-value input,.md-input-container.md-has-value textarea{font-size:16px
}
.md-input-container.md-has-value input,.md-input-container.md-has-value textarea{color:rgba(0,0,0,0.87)
}
.md-input-container.md-input-inline label{pointer-events:none
}
.md-input-container.md-input-inline.md-input-focused label{top:23px;font-size:16px
}
.md-input-container.md-input-inline.md-has-value label{opacity:0
}
.md-input-container.md-input-disabled:after{background:bottom left repeat-x;background-image:linear-gradient(to right, rgba(0,0,0,0.38) 0%, rgba(0,0,0,0.38) 33%, transparent 0%);background-size:4px 1px
}
.md-input-container.md-input-disabled label,.md-input-container.md-input-disabled input,.md-input-container.md-input-disabled textarea{color:rgba(0,0,0,0.38)
}
.md-input-container.md-has-password.md-input-focused .md-toggle-password{color:rgba(0,0,0,0.54)
}
.md-input-container.md-has-password .md-toggle-password{margin:0;position:absolute;right:0;bottom:-2px;color:rgba(0,0,0,0.38)
}
.md-input-container.md-has-password .md-toggle-password .md-ink-ripple{color:rgba(0,0,0,0.87)
}
.md-input-container.md-input-invalid .md-error{opacity:1;transform:translate3d(0, 0, 0)
}
.md-input-container.md-input-required label:after{position:absolute;top:2px;right:0;transform:translateX(calc(100% + 2px));content:"*";font-size:12px;line-height:1em;vertical-align:top
}
.md-input-container.md-has-select:hover .md-select:not(.md-disabled):after{color:rgba(0,0,0,0.87)
}



