
.md-radio{width:auto;margin:16px 8px 16px 0;display:-ms-inline-flexbox;display:inline-flex;position:relative
}
.md-radio .md-radio-container{width:20px;height:20px;position:relative;border-radius:50%;border:2px solid rgba(0,0,0,0.54);transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1)
}
.md-radio .md-radio-container:after{position:absolute;top:3px;right:3px;bottom:3px;left:3px;border-radius:50%;opacity:0;transform:scale3D(0.38, 0.38, 1);transition:all .3s cubic-bezier(0.55, 0, 0.55, 0.2);content:" "
}
.md-radio .md-radio-container input{position:absolute;left:-999em
}
.md-radio .md-radio-container .md-ink-ripple{top:-16px;right:-16px;bottom:-16px;left:-16px;border-radius:50%;color:rgba(0,0,0,0.54)
}
.md-radio .md-radio-container .md-ink-ripple .md-ripple{width:48px !important;height:48px !important;top:0 !important;right:0 !important;bottom:0 !important;left:0 !important
}
.md-radio .md-radio-label{height:20px;padding-left:8px;line-height:20px
}
.md-radio.md-checked .md-radio-container:after{opacity:1;transform:scale3D(1, 1, 1);transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1)
}



