
.md-backdrop{position:absolute;top:0;right:0;bottom:0;left:0;z-index:99;pointer-events:none;background-color:rgba(0,0,0,0.54);transform:translate3d(0, 0, 0);opacity:0;transition:all .5s cubic-bezier(0.35, 0, 0.25, 1)
}
.md-backdrop.md-active{opacity:1;pointer-events:auto
}
.md-backdrop.md-transparent{background:none
}



