
.md-button-toggle{width:auto;display:-ms-flexbox;display:flex
}
.md-button-toggle>.md-button{margin:0;overflow:hidden;border-width:1px 0 1px 1px;border-radius:0;text-align:center;text-overflow:ellipsis;white-space:nowrap
}
.md-button-toggle>.md-button:first-child{border-radius:2px 0 0 2px
}
.md-button-toggle>.md-button:last-child{border-right-width:1px;border-radius:0 2px 2px 0
}
.md-button-toggle>.md-button:not([disabled]){color:rgba(0,0,0,0.54)
}
.md-button-toggle>.md-button:not([disabled]):hover:not(.md-toggle):not(.md-raised){background-color:rgba(153,153,153,0.2);text-decoration:none
}
.md-button-toggle>.md-button .md-ink-ripple{border-radius:2px
}



