
.md-ink-ripple{pointer-events:none;overflow:hidden;position:absolute;top:0;right:0;bottom:0;left:0;-webkit-mask-image:radial-gradient(circle, white 100%, black 100%);mask-image:radial-gradient(circle, white 100%, black 100%);transition:all .3s cubic-bezier(0.55, 0, 0.55, 0.2)
}
.md-ripple{position:absolute;transform:scale(0);background-color:currentColor;opacity:.26;border-radius:50%
}
.md-ripple.md-active{animation:ripple 1s cubic-bezier(0.25, 0.8, 0.25, 1)
}
@keyframes ripple{
to{transform:scale(1.5);opacity:0
}
}



