
.md-tooltip{height:20px;padding:0 8px;position:fixed;z-index:200;pointer-events:none;background-color:rgba(97,97,97,0.87);border-radius:2px;opacity:0;transform-origin:center top;transition:all .4s cubic-bezier(0.25, 0.8, 0.25, 1);transition-duration:.3s;transition-delay:0s;color:#fff;font-family:Roboto,"Noto Sans",Noto,sans-serif;font-size:10px;line-height:20px;text-transform:none;white-space:nowrap
}
.md-tooltip.md-active{opacity:1;transition:all .3s cubic-bezier(0.55, 0, 0.55, 0.2);transition-duration:.3s
}
.md-tooltip:not(.md-active){transition-delay:0s !important
}
.md-tooltip.md-transition-off{transition:none !important
}
.md-tooltip.md-tooltip-top{margin-top:-14px;transform:translate(-50%, 8px)
}
.md-tooltip.md-tooltip-top.md-active{transform:translate(-50%, 0)
}
.md-tooltip.md-tooltip-right{margin-left:14px;transform:translate(-8px, 50%)
}
.md-tooltip.md-tooltip-right.md-active{transform:translate(0, 50%)
}
.md-tooltip.md-tooltip-bottom{margin-top:14px;transform:translate(-50%, -8px)
}
.md-tooltip.md-tooltip-bottom.md-active{transform:translate(-50%, 0)
}
.md-tooltip.md-tooltip-left{margin-left:-14px;transform:translate(8px, 50%)
}
.md-tooltip.md-tooltip-left.md-active{transform:translate(0, 50%)
}



